<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4 d-flex justify-content-between align-items-center">
        <div class="col-sm-12 col-md-12 col-xl-3">
          <h5><span class="text-primary">Busque de Expedientes</span></h5>
        </div>
        <div class="col-sm-12 col-md-6 col-xl-6">
          <div class="d-flex justify-content-end gap-3">
            <div class="col-sm-12 col-md-6 col-xl-7">
              <input type="text" class="form-control w-100" placeholder="Buscar expediente..." v-model="busqueda">
            </div>
            <div class="col-sm-12 col-md-6 col-xl-5">
              <select class="form-select border-1 w-100" v-model.number="proceeding_type">
                <option value="null" selected disabled>Tipo de Expediente</option>
                <option v-for="(item, index) in tipos" :value="item.id" :key="index">{{ item.name }}</option>
              </select>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-hover" aria-describedby="table-expedientes">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>N de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Parte Procesal</th>
                      <th>Materia/Delito</th>
                      <th>Pretensin</th>
                      <th>Fecha de Registro</th>
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-if="expedientesFiltrados.length === 0">
                      <td colspan="8" class="text-secondary text-center">No se encontraron resultados...</td>
                    </tr>
                    <tr v-for="(expediente, index) in expedientesPaginados" :key="index">
                      <td>{{ primerRegistro + index }}</td>
                      <td>
                        <a href="javascript:void(0)" class="btn-link" @click="redirectDetalle(expediente.exp_id)">
                          {{ expediente.numero }}
                        </a>
                      </td>
                      <td>{{ $formatDate(expediente.fecha_inicio) }}</td>
                      <td>{{ getNombreCompleto(expediente.procesal, expediente.multiple) }}</td>
                      <td>{{ expediente.materia }}</td>
                      <td>{{ expediente.pretencion }}</td>
                      <td>{{ $formatDate(expediente.creacion) }}</td>
                      <td>
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <nav aria-label="..." class="d-flex justify-content-between align-items-center">
                <div>
                  <span>Mostrando {{ expedientesPaginados.length === 0 ? '0' : primerRegistro }} a {{ ultimoRegistro }} de
                    {{ expedientesFiltrados.length }}
                    registros</span>
                </div>
                <ul class="pagination">
                  <li class="page-item" :class="{ disabled: currentPage === 1 }">
                    <a class="page-link" href="javascript:void(0)" @click="prevPage">Anterior</a>
                  </li>
                  <li class="page-item" :class="{ disabled: currentPage === totalPages }">
                    <a class="page-link" href="javascript:void(0)" @click="nextPage">Siguiente</a>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import feather from 'feather-icons';
import DataTable from '@/utils/dataTable';
import { badgeClass } from '@/utils/utils.js';
import axios from '@/plugins/axios.js';
import { debounce } from 'lodash';

export default {
  name: 'Buscar',
  // mixins: [DataTable],
  data() {
    return {
      tipoSeleccionado: "",
      tipos: [],
      expedientes: [],
      dataTable: null,
      currentPage: 1,
      itemsPerPage: 10,
      busqueda: '',
      proceeding_type: null,
    };
  },
  watch: {
    proceeding_type: {
      handler(newVal, oldVal) {
        // Cuando cambia proceeding_type, restablece la pgina actual y aplica el filtro
        this.currentPage = 1;
      },
     
      immediate: true
    }
  },
  updated() {
    feather.replace();
  },
  mounted() {
    this.cargartiposExpedientes();
    this.cargarExpedientes();
  },
  computed: {
    id() {
      return this.$store.getters.get__user.abo_id;
    },
    // Filtra los expedientes segn la bsqueda actual
    expedientesFiltrados() {
      return this.expedientes.filter(expediente => {
        return (
          expediente.numero.toLowerCase().includes(this.busqueda.toLowerCase()) ||
          expediente.pretencion.toLowerCase().includes(this.busqueda.toLowerCase()) ||
          expediente.materia.toLowerCase().includes(this.busqueda.toLowerCase()) ||
          expediente.estado_proceso.toLowerCase().includes(this.busqueda.toLowerCase()) ||
          expediente.procesal.some(proceso => {
            if (!proceso) return false;
            return (
              proceso.tipo_procesal.toLowerCase().includes(this.busqueda.toLowerCase()) ||
              proceso.tipo_persona.toLowerCase().includes(this.busqueda.toLowerCase()) ||
              proceso.dni && proceso.dni.toString().toLowerCase().includes(this.busqueda.toLowerCase()) ||
              proceso.apellido_paterno && proceso.apellido_paterno.toLowerCase().includes(this.busqueda.toLowerCase()) ||
              proceso.apellido_materno && proceso.apellido_materno.toLowerCase().includes(this.busqueda.toLowerCase()) ||
              proceso.nombres && proceso.nombres.toLowerCase().includes(this.busqueda.toLowerCase())
            );
          }) ||
          expediente.tipo_exp === this.proceeding_type
        );
      });
    },
    // Calcula el nmero total de pginas
    totalPages() {
      return Math.ceil(this.expedientesFiltrados.length / this.itemsPerPage);
    },
    // Calcula el ndice del primer registro en la pgina actual
    primerRegistro() {
      return (this.currentPage - 1) * this.itemsPerPage + 1;
    },
    // Calcula el ndice del ltimo registro en la pgina actual
    ultimoRegistro() {
      const ultimo = this.currentPage * this.itemsPerPage;
      return ultimo > this.expedientesFiltrados.length ? this.expedientesFiltrados.length : ultimo;
    },
    // Devuelve los expedientes de la pgina actual basados en la paginacin
    expedientesPaginados() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.expedientesFiltrados.slice(startIndex, endIndex);
    },
  },

  methods: {
    // Mtodo para cambiar a la pgina siguiente
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },

    // Mtodo para cambiar a la pgina anterior
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    async cargartiposExpedientes() {
      try {
        const response = await axios.get('proceedingTypes');
        console.log(response);
        this.tipos = response.data;
        console.log('tipos => ' + this.tipos);
        // this.tipoSeleccionado = response[0];
      } catch (error) {
        this.$handleApiError(error, 'cargar tipos de expedientes');
      }
    },

    async cargarExpedientes() {
      try {
        this.$showLoadingAlert();
        const formData = {
          'abo_id': this.id
        }

        let response = null;
        if (this.id === null) {
          response = await axios.get('proceeding', {
            'abo_id': this.id
          })
          console.log(response);
          // response = await this.$getData('proceeding', this);
        } else {
          response = await this.$postData('lawyer/expedientes', this, formData);
        }
        this.expedientes = response.data.data;
        this.initDataTable();
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'cargar expedientes');
      }
    },
    redirectDetalle(id) {
      this.$router.push({ name: 'expediente/detalle', params: { id } });
    },
    getNombreCompleto(procesal, multiple) {
      const tipoProcesal = procesal[0].tipo_procesal;
      return this.esDemandanteODemandado(tipoProcesal) ? this.obtenerNombreCompleto(procesal, multiple) : 'UNPRG';
    },
    esDemandanteODemandado(tipoProcesal) {
      return tipoProcesal === 'DEMANDANTE' || tipoProcesal === 'DEMANDADO';
    },
    obtenerNombreCompleto(procesal, multiple) {
      if (multiple === '1') {
        return 'Demanda Colectiva';
      }

      const { tipo_persona, apellido_paterno, apellido_materno, nombres, razon_social } = procesal[0];
      return tipo_persona === 'NATURAL' ? `${apellido_paterno} ${apellido_materno} ${nombres}` : razon_social;
    },
    badgeClass,
    formatFecha(fechaISO) {
      const fecha = new Date(fechaISO);
      const opcionesFormato = {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        second: 'numeric'
      };
      return fecha.toLocaleDateString('es-ES', opcionesFormato);
    },
    formatDate(fechaISO) {
      const fecha = new Date(fechaISO);
      const opcionesFormato = {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
      };
      return fecha.toLocaleDateString('es-ES', opcionesFormato);
    },

    async initDataTable() {
      this.destroyDataTable();
      await this.$nextTick();
      this.dataTable = $(this.$refs.dataTable).DataTable({
        language: {
          url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
        },
      });
    },

    destroyDataTable() {
      this.dataTable && this.dataTable.destroy() && (this.dataTable = null);
    },

    debounceSearch: debounce(function () {
      this.currentPage = 1; // Restablece la pgina actual al buscar
    }, 300),
  },
};
</script>

<style scoped>
@import '@/assets/css/fileStatus.css';
@import '@/assets/css/dataTable.css';
</style>