// src/plugins/axios.js

import axios from 'axios';
import store from '@/store'; // Importa tu store Vuex

const instance = axios.create({
  baseURL: store.getters.get__url, // Usa la URL de tu store Vuex
  timeout: 10000, // Tiempo mximo de espera para las solicitudes (en milisegundos)
});

// Interceptor para agregar el token de autorizacin a cada solicitud
instance.interceptors.request.use(
  config => {
    const token = store.getters.get__token; // Obtiene el token del store Vuex
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

export default instance;
